import React, { useState } from "react";
import DisplayWeather from "./DisplayWeather";
import "./weather.css";
//import { Button, Form, Input, FormGroup } from "reactstrap";
import { Button } from "reactstrap";
import Form from "react-bootstrap/Form";
import amsterdam from "../amsterdam.jpg";
import london from "../london.jpg";
import chennai from "../chennai.jpg";
import paris from "../paris.jpg";
import Table from "react-bootstrap/Table";

function Weather() {
  const [weather, setWeather] = useState([]);
  const [form, setForm] = useState({
    city: "",
  });

  const APIKEY = "9431937f07c0916cdbffc345d9c7e758";
  async function weatherData(e) {
    e.preventDefault();
    if (form.city === "") {
      alert("Add values");
    } else {
      const output = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${form.city}&APPID=${APIKEY}`
      ).then((res) => res.json());
      const lat = output.coord.lat;
      const lon = output.coord.lon;
      const data = await fetch(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&appid=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());
      setWeather({ data });
      console.log(data);
    }
  }

  async function preData(e) {
    if (e === "amsterdam") {
      const output = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=amsterdam&APPID=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());
      const lat = output.coord.lat;
      const lon = output.coord.lon;
      const data = await fetch(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&appid=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());

      setWeather({ data });
      console.log(data);
    }

    if (e === "london") {
      const output = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=london&APPID=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());
      const lat = output.coord.lat;
      const lon = output.coord.lon;
      const data = await fetch(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&appid=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());

      setWeather({ data });
      console.log(data);
    }

    if (e === "paris") {
      const output = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=paris&APPID=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());
      const lat = output.coord.lat;
      const lon = output.coord.lon;
      const data = await fetch(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&appid=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());

      setWeather({ data });
      console.log(data);
    }

    if (e === "chennai") {
      const output = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=chennai&APPID=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());
      const lat = output.coord.lat;
      const lon = output.coord.lon;
      const data = await fetch(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&appid=9431937f07c0916cdbffc345d9c7e758`
      ).then((res) => res.json());

      setWeather({ data });
      console.log(data);
    }
  }

  const handleChange = (e) => {
    let name = e.target.name;
    let value = e.target.value;

    if (name === "city") {
      setForm({ ...form, city: value });
    }
  };
  return (
    <div className="weather">
      <Table>
        <tr>
          <td style={{ borderTop: "#ffffff" }}>
            <Form.Control
              placeholder="City"
              name="city"
              id="city"
              onChange={(e) => handleChange(e)}
            />
            <br></br>
            <Button
              className="getweather"
              variant="primary"
              onClick={(e) => weatherData(e)}
            >
              Submit
            </Button>
          </td>
        </tr>
        <tr>
          <td style={{ borderTop: "#ffffff" }}>
            <h4>Weather Forecast</h4>
          </td>
        </tr>
        <tr>
          <td style={{ borderTop: "#ffffff" }}>
            <Button id="amsterdam" onClick={(e) => preData("amsterdam")}>
              <img
                src={amsterdam}
                alt=""
                height="130px"
                width="170px"
                style={{ borderRadius: "10px" }}
              ></img>
            </Button>
            <h6>Amsterdam</h6>
          </td>

          <td style={{ borderTop: "#ffffff" }}>
            <Button id="london" onClick={(e) => preData("london")}>
              <img
                src={london}
                alt=""
                height="130px"
                width="170px"
                style={{ borderRadius: "10px" }}
              ></img>
            </Button>
            <h6>London</h6>
          </td>
          <td style={{ borderTop: "#ffffff" }}>
            <Button id="paris" onClick={(e) => preData("paris")}>
              <img
                src={paris}
                alt=""
                height="130px"
                width="170px"
                style={{ borderRadius: "10px" }}
              ></img>
            </Button>
            <h6>Paris</h6>
          </td>
          <td style={{ borderTop: "#ffffff" }}>
            <Button id="chennai" onClick={(e) => preData("chennai")}>
              <img
                src={chennai}
                alt=""
                height="130px"
                width="170px"
                style={{ borderRadius: "10px" }}
              ></img>
            </Button>
            <h6>Chennai</h6>
          </td>
        </tr>
      </Table>
      {/* {console.log(weather)} */}
      {weather.data !== undefined ? (
        <div>
          <DisplayWeather data={weather.data} />
        </div>
      ) : null}
    </div>
  );
}

export default Weather;
